$basicLightbox__background: rgba(127, 127, 127, 0.8); // Background color
$basicLightbox__zIndex: 1000; // Stack order
$basicLightbox__duration: 450ms; // Transition duration
$basicLightbox__timing: ease-out; // Transition timing

@import 'basiclightbox/src/styles/main';

.basicLightbox {
  @include respond-below($tablet) {
    align-items: flex-start;
    overflow-y: scroll;
  }

  &.trailer {
    align-items: center;
  }
}
