@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellip,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 40px;
  margin-bottom: 40px;

  line-height: normal;
  text-align: center;

  @include respond-above($tablet) {
    margin-bottom: 60px;
  }
}

.tui-pagination .tui-page-btn {
  position: relative;
  width: 40px;
  padding: 13px 13px;
  margin-left: 4px;
  color: $movie-title-black-color;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 5px;

  transition: $btn-animation;
}

.tui-pagination .tui-page-btn:hover {
  color: $white-text-color;
  background-color: $page-accent-bg-color;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: $white-text-color;
  background: $page-accent-bg-color;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: $page-accent-bg-color;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  white-space: nowrap;
  background-color: $arrow-bg-color;
  font-weight: normal;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 5px;
  background-color: transparent;
  line-height: 1;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 5px;
  margin-top: 23px;
  vertical-align: bottom;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: $arrow-bg-color;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}
s .tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-first-is-hidden,
.tui-last-is-hidden,
.tui-ellip-is-hidden {
  @include respond-below($tablet) {
    display: none;
  }
}

.tui-pagination-is-hidden {
  display: none;
}
